  created() {
    this.getCateList()
  },
  methods: {
    // 商品分类数据
    async getCateList() {
      let { data: res } = await this.$axios.get('categories')
      if (res.meta.status !== 200)
        return this.$message.error('请求分类数据失败')
      this.cateList = res.data
    },
    // 级联选择框改变
    handleChange() {
      this.getParamsData()
    },
    // tab页签点击事件
    handleClick() {
      this.getParamsData()
    },
    // 获取数据
    async getParamsData() {
      if (this.selectedOptions.length !== 3) return
      let { data: res } = await this.$axios.get(
        `categories/${this.cateId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      if (res.meta.status !== 200)
        return this.$message.error('请求分类参数失败')
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 点击按钮添加参数
    addParams() {
      this.$refs.paramsForm.validate(async valid => {
        if (!valid) return
        let { data: res } = await this.$axios.post(
          `categories/${this.cateId}/attributes`,
          {
            attr_name: this.paramsForm.attr_name,
            attr_sel: this.activeName
          }
        )
        if (res.meta.status !== 201) return this.$message.error('添加失败')
        this.$message.success('添加成功')
        this.addDynamicParamsVisbile = false
        this.getParamsData()
      })
    }
  },
  computed: {
    // 如果按钮需要禁用 返回true
    isBtnNeedForbidden() {
      if (this.selectedOptions.length !== 3) {
        return true
      }
      return false
    },
    // 三级分类的id
    cateId() {
      if (this.selectedOptions.length === 3) {
        return this.selectedOptions[2]
      }
      return null
    },
    // dialog的title名称
    dialagTitle() {
      if (this.activeName === 'many') return '添加动态参数'
      return '添加静态属性'
    }
  }
